.Grid {
  padding: 5px;
  background: #baa;
  width: 338px;
  margin: auto;
  border-radius: 7px; }
  .Grid-container {
    width: 338px;
    margin: 10px auto;
    position: relative; }
  .Grid-row {
    display: flex;
    justify-content: center; }
  .Grid-tile {
    width: 76px;
    height: 76px;
    line-height: 76px;
    align-items: center;
    border: solid 1px #ccc;
    border-radius: 4px;
    text-align: center;
    margin: 3px;
    font-size: 48px;
    font-weight: bold;
    color: #666;
    background: rgba(238, 228, 218, 0.35);
    transition: background 0.3s, color 0.3s ease-in, font-size 0.5s ease-in-out; }
  .Grid-overlay {
    position: absolute;
    top: 0;
    left: 0;
    background: #ccc;
    opacity: 0.8;
    width: 348px;
    height: 348px;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 40px; }
    .Grid-overlay-message {
      opacity: 1;
      color: tomato;
      font-size: 40px; }

