body {
  background-color: white;
  overscroll-behavior: none; }

* {
  font-family: Arial, Helvetica, sans-serif; }

.heading {
  text-align: center;
  color: #666;
  font-size: 40px; }

.re {
  background: #db4d3f;
  color: white;
  font-size: 40px;
  padding: 20px 2px 0 10px;
  margin-right: 1px; }

.controls {
  width: 340px;
  margin: auto;
  margin-bottom: 10px;
  text-align: right; }

.new-game {
  border: solid 1px #ccc;
  border-radius: 4px;
  background: #baa;
  font-size: 20px;
  padding: 4px 8px;
  cursor: pointer;
  color: #fff;
  font-weight: bold; }

.hint {
  margin-top: 10px;
  text-align: center;
  color: #999;
  font-weight: bold; }
